import React from "react";
import classes from './MyPosts.module.css';
import Post from "./Post/Post";
import {ErrorMessage, Field, Form, Formik} from "formik";
import {AddMessageSchema} from "../../FormValidators/ValidationFormSchema";



const MyPosts = React.memo((props) => {

    let postElement = props.posts.map((p) => {
        return (<Post message={p.message} likes={p.likes}/>)
    })

    return (<div className={classes.postsBlock}>
        <h3>My Posts</h3>
        <Formik
            initialValues={{ message: ''}}
            validationSchema={AddMessageSchema}
            onSubmit={(values, { setSubmitting }) => {
                console.log(values)
                props.addPost(values.message)
                setSubmitting(false);
            }}
        >
            {({ isSubmitting }) => (
                <Form>
                    <div>
                        <Field type="textarea" name="message" placeholder = "enter message here"/>
                        <ErrorMessage name="message" component="div" className={classes.error} />
                    </div>
                    <div>
                        <button type="submit" disabled={isSubmitting}>
                            Add post
                        </button>
                    </div>
                </Form>
            )}
        </Formik>
        <div className={classes.posts}>
            {postElement}
        </div>
    </div>)
})

export default MyPosts;